import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Schedule from './panels/Schedule';
import Price from './panels/Price';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
			authToken : null
		};
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				case 'VKWebAppAccessTokenReceived':
						this.setState({ authToken : e.detail.data.access_token });
						this.getWiget()
						break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
		connect.send("VKWebAppGetAuthToken", {"app_id": 7131693, "scope": "app_wiget"});


	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	getWiget() {
		console.log('Wiget!');
		
	}

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} />
				<Schedule id="schedule" go={this.go}/>
				<Price id="price" go={this.go}/>
			</View>
		);
	}
}

export default App;
